
.playlist-tools {
  display: inline-block;
  z-index: 99;
}
.pt .tool {
  display: inline-block;
  background: #fff;
  width: 44px;
  height: 44px;
  line-height: 52px;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  -webkit-box-shadow: 0 3px 3px 0 rgba(0,0,0,0.14), 0 1px 7px 0 rgba(0,0,0,0.12), 0 3px 1px -1px rgba(0,0,0,0.2);
          box-shadow: 0 3px 3px 0 rgba(0,0,0,0.14), 0 1px 7px 0 rgba(0,0,0,0.12), 0 3px 1px -1px rgba(0,0,0,0.2);
  -webkit-transition: color, background 0.2s;
  transition: color, background 0.2s;
  color: #000;
}
.pt .tool:hover {
  color: #fff;
  background: #000;
}
.pt .add-box {
  position: absolute;
  margin-top: 5px;
  z-index: 99;
  width: 30%;
}
.pt .yt-url {
  display: block;
  width: 100%;
  font-size: .6em;
  margin: 0;
}
.pt .add-box button {
  width: calc(100% + 2.4em);
  color: #000;
  font-size: .4em;
  -webkit-transition: color, background 0.3s;
  transition: color, background 0.3s;
}
.pt .add-box button:hover {
  color: #fff;
  background: #000;
}
.pt .add-box button .material-icons {
  position: relative;
  top: 2px;
}

/* Get Songs From Youtube Component */
.pt .yt-video-details {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  position: relative;
  left: initial;
  -webkit-transform: none;
          transform: none;
  font-size: .6em;
}
.pt .yt-video-details p {
  font-size: .9em;
}
.pt .add-icon {
  position: absolute;
  font-size: 1.2em;
  left: 0;
  bottom: 0;
  z-index: 999;
  -webkit-transition: font-size .1s;
  transition: font-size .1s
}
.pt .add-icon:hover {
  font-size: 1.4em;
}
.pt .yt-url {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.pt .yt-label {
  display: none;
}



