
.playlist-page {
  padding: 30px 30px;
  color: #fff;
  font-weight: bold;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  overflow: auto;
}
.col {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  margin: 0 15px;
}
.col.left {
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
  width: calc(100% - 520px);
}
.col.right {
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
}
.playlist-details {
  font-size: 1.8em;
  margin-bottom: 10px;
}
.author-wrapper {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin-bottom: 5px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  cursor: pointer;
}
.playlist-details .title {
  display: block;
  font-size: 1.3em;
}
.author-wrapper:hover {
  color: #000;
}
.playlist-page .material-icons.home {
  position: relative;
  bottom: 1px;
  left: 5px;
  font-size: 1.15em;
  -webkit-transition: bottom 0.1s;
  transition: bottom 0.1s;
}
.author-wrapper:hover .home {
  color: #000;
  bottom: 5px;
}
.playlist-page .slider-cover {
  position: fixed;
  top: 100vh;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  background: -webkit-gradient(linear, left top, left bottom, from(#9733EE), to(#DA22FF)) no-repeat;
  background: linear-gradient(to bottom, #9733EE, #DA22FF) no-repeat;
  -webkit-transition: top .5s;
  transition: top .5s;
}
.slider-cover.active {
  top: 0;
  opacity: 1;
  z-index: 999;
}
@media only screen and (max-width: 913px) {
.playlist-page {
    min-width: 913px;
    -ms-flex-preferred-size: auto;
        flex-basis: auto;
    -webkit-box-flex: 0;
        -ms-flex: 0;
            flex: 0;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}
body {
    overflow: hidden;
}
}



