
@import url('https://fonts.googleapis.com/css?family=Hammersmith+One');
@import url('https://fonts.googleapis.com/icon?family=Material+Icons');
@import './assets/css-reset.css';
html, body {
  height: 100%;
  margin: 0;
}
#app {
  background: #DA22FF no-repeat;  /* fallback for old browsers */ 
  background: -webkit-gradient(linear, left top, left bottom, from(#9733EE), to(#DA22FF)) no-repeat; 
  background: linear-gradient(to bottom, #9733EE, #DA22FF) no-repeat;
  height: 100%;
  font-family: 'Hammersmith One', sans-serif;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  min-width: 680px;
}
.et-wrapper {
  font-family: 'Hammersmith One', sans-serif;
  width: 50%;
  max-width: 50%;
  background: rgba(48,45,55,.9);
  left: 50% !important;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  top: auto !important;
  bottom: 0 !important;
  right: auto !important;
}



