
.example-playlist-creator {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  height: calc(100% - 60px);
  margin: auto;
  text-align: center;
  color: #fff;
  overflow-y: auto;
}
.example-playlist-creator h1 {
  margin-top: 0;
}
.steps {
  width: 100%;
  max-height: calc(100% - 60px);
}
.steps input {
  display: block;
  width: 45%;
  margin: auto;
  margin-bottom: 15px;
  font-size: 1.4em;
}

/* Step 2 */
.steps .s2 .add-icon {
  font-size: 52px;
}
.added-songs-list {
  font-size: 1.2em;
}

/* Step 3 */
.steps .s3 {
  margin-bottom: 52px;
}
.btn-newAccount {
  display: block;
  width: 50%;
  font-size: 1.4em;
  margin: auto;
  margin-bottom: 15px;
}
.example-playlist-creator .material-icons {
  font-size: 32px;
  cursor: pointer;
}
.arrows .material-icons {
  position: fixed;
  bottom: 15px;
}
.arrows .left {
  left: calc(100% - 97.5%);
}
.arrows .right {
  right: calc(100% - 97.5%);
}




