
.yt-video-details {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  width: calc(45% + 1.5em);
  height: 124px;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  font-size: 1.4em;
}
.yt-video-details img {
  float: left;
  height: 100%;
}
.yt-video-details p {
  height: 100%;
  text-align: left;
  padding: 0 0.2em;
  overflow-y: auto;
  overflow-x: hidden;
}



