
.user-playlists {
  height: 100%;
  padding: 30px 45px;
  overflow: auto;
}
.user-playlists .header {
  font-size: 2.2em;
  font-weight: bold;
  margin-bottom: 15px;
}
.user-playlists .title {
  display: inline-block;
  margin: 0 0 15px 0;
}
.btn-logout {
  position: relative;
  bottom: 0;
  float: right;
  color: #fff;
  cursor: pointer;
  -webkit-transition: bottom 0.1s;
  transition: bottom 0.1s;
}
.btn-logout:hover {
  bottom: 5px;
}
.playlists-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-right: -10px;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
}
.playlist-box {
  position: relative;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  margin: 0 5px;
  width: 22%;
  padding: 10px;
  margin-bottom: 15px;
  color: #ebebeb;
  font-weight: bold;
  font-size: 1em;
  background: rgba(0,0,0,0.1);
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  cursor: pointer;
  -webkit-transition: bottom 0.1s;
  transition: bottom 0.1s;
}
.playlist-box:hover {
  bottom: 5px;
}
.playlist-box img {
  width: 100%;
  height: 140px;
  margin-bottom: 15px;
}
.playlist-box .p-size {
  font-size: .8em;
  opacity: .8;
  margin-bottom: 7.5px;
}
.btn-delete {
  position: absolute;
  right: 15px;
  font-size: 2em;
  color: #FF5964;
  margin-top: 2px;
  margin-left: 2px;
  -webkit-transition: font-size 0.1s;
  transition: font-size 0.1s;
}
.btn-delete:hover {
  font-size: 2.4em;
}
.nothing-here {
  display: block;
}
.playlists-list-enter-active, .song-list-leave-active {
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.playlists-list-enter, .song-list-leave-to {
  opacity: 0;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
}
.user-playlists .slider-cover {
  position: fixed;
  top: -100vh;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  background: -webkit-gradient(linear, left top, left bottom, from(#9733EE), to(#DA22FF)) no-repeat;
  background: linear-gradient(to bottom, #9733EE, #DA22FF) no-repeat;
  -webkit-transition: top .5s;
  transition: top .5s;
}
.user-playlists .slider-cover.active {
  top: 0;
  opacity: 1;
  z-index: 999;
}
.user-playlists .logout-cover {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  opacity: 0;
  z-index: -10;
  -webkit-transition: opacity .1s;
  transition: opacity .1s;
}
.user-playlists .logout-cover.active {
  z-index: 999;
  opacity: 1;
}
@media only screen and (max-width: 1130px) {
.playlist-box {
    width: 30%;
}
}
@media only screen and (max-width: 995px) {
.playlist-box {
    width: 258.500px;
}
}




