
.related-list {
  width: 400px;
  overflow: auto;
}
.related-list .song {
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 22px;
}
.related-list .song img {
  width: 130px;
  height: 90px;
  margin-right: 5px;
}



